$red: #ff3d37;
$white: #ffffff;
@font-face {
  font-family: SpaceMono;
  src: url(./assets/SpaceMono-Italic.ttf) format("opentype");
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'SpaceMono';
}

button, .roundedBorders {
	border-radius: 5px;
	padding: 5px;
}

button {
		border-color: $red;
		color: $white;
		background-color: $red;
		transition: all 150ms ease-in-out;
		outline:none;

		&:hover {
			color: rgb(0, 0, 0);
			background-color: $white;
			box-shadow: 0 0 10px 0 $red inset, 0 0 10px 4px $red;
		}
}

.backdrop {
	left: 0;
	top: 0;
	width: 100%;
	min-height: 100%;
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(110, 110, 110, 0.5);
	z-index: 1;
}

.close {
	position: absolute;
	top: 0;
	right: 5px;
	font-size: 2em;
	font-family: sans-serif;
	transform: rotate(45deg);
	cursor: pointer;
}

.modalWrapper {
	position: absolute;
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-direction: column;
	border-radius: 5px;
	padding: 10px;
	border: 2px solid black;
}
